import React, {
    Component
} from "react"

class P5component extends Component {

    componentDidMount() {
        const p5 = require('p5')

        window.sketchComponent = new p5(this.props.sketch)
    }

    componentDidUpdate() {
        const p5 = require('p5')

        window.sketchComponent = ""

        var node = document.getElementsByTagName("body")[0]
        node.removeChild(node.childNodes[node.childNodes.length - 1]);


        window.sketchComponent = new p5(this.props.sketch)
    }


    componentWillUnmount() {
        window.sketchComponent = ""

        var node = document.getElementsByTagName("body")[0]
        node.removeChild(node.childNodes[node.childNodes.length - 1]);

    }

    render() {
        return <div />
    }
}

export default P5component